import React from 'react'
import ServicesBox from '../services/ServicesBox.jsx'
import ResearchContent from './ResearchContent.jsx'

class ResearchScreen extends React.Component{
      
    constructor(){
        super()
        this.state = {currentContent:0}
        this.setContent = this.setContent.bind(this)
    }
    
    setContent(id){
        this.setState({currentContent:id})
    }
    render(){
        return(
            <div className='researchScreen'>
                <div className='researchCont'>
                    <ServicesBox setContent={this.setContent} id='1' src='./iconAI.png' title='Automatizované spracovanie neštrukturovaného obsahu' stage='Projekt ukončený' />
                    <ServicesBox setContent={this.setContent} id='2' src='./iconData.png' title='Centrum pre biomedicínsky výskum – BIOMEDIRES' stage='II. etapa'/>
                </div>
                {!this.props.swipeState &&
                <ResearchContent swipeState={this.props.swipeState} currentContent = {this.state.currentContent} />
                    }
            </div>
        )
    }
}

export default ResearchScreen