import React from 'react'

class BioMerides extends React.Component{
    constructor(){
        super()
    }
    render(){
        return(
        <div className='references content'>
            <h2>Centrum pre medicínsky výskum - BIOMERIDES</h2>
                <div className='fade'></div>
                <img src={require('../references/img/decor.png')} className='refDecor one'></img>
                <img src={require('../references/img/decor2.png')} className='refDecor two'></img>
            <div className='mainCont'>
                <div className='mainInfo'>
                    <h2>Ciele projektu</h2>
                    <p>Výskumno-vývojový zámer je orientovaný na výskum nových inovatívnych diagnostických a prognostických markerov a terapeutických cieľov v oblasti nádorových ochorení žien, so zameraním sa primárne na endometriálny karcinóm a chorobné zmeny krčka maternice s využitím moderného multidisciplinárneho prístupu (okrem klasického biomedicínskeho zamerania sú využívané aj výskumné prístupy v oblasti materiálového výskumu/senzorov a moderných informačných technológií) a vzájomnou integráciou výsledkov výskumu a vývoja.</p>
                    <p>
                        <div className='researchGarants'>
                    <img className='research refImg opvai' src={require('./img/opvai_logo.png')}></img>
                    <img className='research refImg' src={require('./img/msvvs_logo.png')}></img>
                    <img className='research refImg' src={require('./img/fond_logo.jpg')}></img>
                    <img className='research refImg' src={require('./img/vyskumna_agentura_logo.png')}></img>
                    </div>
                    Projekt je založený na multidisciplinárnom prístupe pri analýze potenciálnych markerov nádoru endometria. Know-how MGA aj jeho partnerov je predpokladom pre úspešnú integráciu tak klinických, histomorfologických, imunohistochemických, genetických, genomických, proteomických ako aj glykomických analýz. V projekte taktiež využívame nové prístupy na báze inovatívnych biotechnológií (biosenzory). Pomocou nich predpokladáme zavedenie jednoduchej metódy na analýzu zloženia mikroprostredia pomocou zmien tlaku intersticiálnej tekutiny nádoru. Očakávaná šírka dát vyžaduje následnú inteligentnú počítačovú analýzu. Výsledkom takejto analýzy by mal byť prediktívny model správania sa nádoru. Analýza premorenosti populácie HPV umožní lepšie pochopenie stavu slovenskej populácie, čo je dôležité pre procesy zavádzania očkovania v súčasnosti. Aplikácia nízkodimenzionálnych nanoobjektov v biomedicine je interdisciplinárna problematika, kombinuje nanotechnológie, nanovedu a materiálovú vedu s chémiou, biomedicínou a onkológiou. Predložený projekt prináša aj vklad z oblasti nanotechnológie, nanovedy a materiálovej vedy vrátane pokročilých experimentálnych metód.</p><p>
Hlavným vedeckým cieľom projektu je identifikácia markerov s vysokým potenciálom využitia v diagnostickej praxi v zdravotníctve, s potenciálom zlepšiť diagnostiku, prognostické hodnotenie, ako aj identifikovať potenciálne nové terapeutické ciele pacientov</p><p>
Pomocou optimalizácie diagnostiky najčastejších nádorov maternice s využitím moderných diagnostických a laboratórnych postupov, s ohľadom na možné využitie v neinvazívnej diagnostike a s ohľadom na identifikáciu hereditárnych foriem nádorov je dlhodobým cieľom zníženie incidencie a mortality tretej najčastejšej nádorovej choroby u žien.
Ďalším vedeckým cieľom projektu je aj vytvorenie modelu vzniku, správania sa, prognózy a reakcie na terapiu endometriálneho karcinómu.
                    </p>
            </div>
                <div className='bonusInfo'>
                    <div>
                        <h3>Momentálny stav</h3>
                        <p>II. etapa</p>
                        <h3>Kód výzvy</h3>
                        <p>OPVaI-VA/DP/2018/2.2.1-01</p>
                        <h3>Kód projektu v ITMS2014+</h3>
                        <p>313011W428</p>
                        <h3>Prijímateľ</h3>
                        <p>MEDIREX GROUP ACADEMY n.o.</p>
                        <h3>Obdobie realizácie projektu</h3>
                        <p>01/2019 – 06/2023</p>
                    </div>
                    <div>
                        <h3>Partneri</h3>
                        <p>Centrum experimentálnej medicíny Slovenskej akadémie vied <br></br> Chemický ústav Slovenskej akadémie vied <br></br> MindIT, s.r.o. <br></br> Univerzita Komenského v Bratislave</p>
                    </div>
                    <div>
                        <h3>Miesta realizácie</h3>
                        <p>Banská Bystrica<br></br>Nitra<br></br>Bratislava – mestská časť Staré Mesto<br></br>Bratislava – mestská časť Karlova Ves</p>
                    </div>
            </div>
            </div>
        </div>
        )
    }
}

export default BioMerides