class ReferencesScreen extends React.Component{
    
    constructor() {
        super()
        this.state = {
            currentScreen : 1,
            currentContent : 0
        }
        this.handleClick = this.handleClick.bind(this)
        this.setContent = this.setContent.bind(this)
    }
    
    handleClick(id){
        this.setState({currentScreen : id,
                      currentContent : 0})
    }
    
    setContent(id){
        this.setState({currentContent : 1})
    }
    
    
    render(){
        var thisref = this
        const referencesToRender = referenceSets[this.state.currentScreen].map((item) => {
            return (
            <ReferencesCont setContent={thisref.setContent} key={item.id} data={item} />
            )
        })
        
        const buttonsToRender = buttonsSet.map((item) => {
            return (
            <ChooserButton 
                key={item.id} 
                data={item} 
                onclick={this.handleClick} 
                active={this.state.currentScreen == item.id ? true : false}
            ></ChooserButton>
            )
        })
        
        return(
                <div className='referencesScreen'>
                    <div style={{minHeight:'100vh'}}>
                    <div style={{display:'flex',justifyContent:'center',width:'100%'}}>
                        <div className='referencesChooser'>
                            {buttonsToRender}
                        </div>
                    </div>
                    <div style={{display:'flex',justifyContent:'center',width:'100%',marginTop:'50px'}}>
                        <div className='referencesWrapper'>
                                {referencesToRender}
                        </div>
                    </div>
                        </div>
                {!this.props.swipeState &&
                <ReferencesContent swipeState={this.props.swipeState} currentContent = {this.state.currentContent} />
                    }
                </div>
        )
    }
}

export default ReferencesScreen