import React from 'react'

class Sporitelna extends React.Component{
    constructor(){
        super()
    }
    render(){
        return(
        <div className='references content'>
            <h2>Digitalizácia dokumentov v Slovenskej sporiteľni</h2>
                <div className='fade'></div>
                <img src={require('./img/decor.png')} className='refDecor one'></img>
                <img src={require('./img/decor2.png')} className='refDecor two'></img>
            <div className='mainCont'>
                <div className='mainInfo'>
                    <h2>Zadanie</h2>
                    <p>Slovenská sporiteľňa, a.s. (ďalej len SLSP) vo svojom Retailovom Procesnom Centre (ďalej len RPC) spracúva došlú papierovú korešpondenciu za celú pobočkovú sieť. S cieľom digitalizovať dokumenty a zároveň znížiť dobu trvania procesu prepisovania údajov z papierových dokumentov do informačného systému nás SLSP oslovila s požiadavkou vytvorenia Konceptu riešenia (PoC) automatizácie vyťažovania a spracovania textov z rôznych druhov skenovaných dokumentov. Na dokumente „Informácia o platobnom účte spotrebiteľa“ bolo našou úlohou identifikovať a vyťažiť požadované klientské údaje vrátane informácií o nastavených inkasách a trvalých príkazoch na danom účte, a tieto v definovanom formáte poskytnúť na prenos do cieľového informačného systému. Automatizácia mala z pohľadu SLSP splniť KPI - skrátenie času pre spracovanie danej agendy pod 17 minút. Zároveň klient očakával presnosť vyťaženia jednotlivých údajov na úrovni 90%.
                    </p>
                    <img className='references refImg' src={require('./img/sken.png')}></img>
                    <h2>Riešenie</h2>
                    <p>Naša aplikácia na inteligentné vyťažovanie dát DocMiner bola pre účely klienta customizovaná tak, aby po spracovaní dokumentu pomocou OCR technológie (ABBYY FineReader) a prevode textu do dátového tvaru identifikovala na základe definovaných algoritmov, kritérií a pravidiel potrebné údaje, tieto validovala a agregovala do dátovej vety požadovaného formátu. DocMiner zároveň umožnil hromadné spracovanie, teda spracovanie viacerých dokumentov naraz, a to automaticky, bez manuálneho spúšťania. </p>
                    <h2>Výsledok</h2>
                    <p>Dodané riešenie umožnilo hromadné spracovanie desiatok dokumentov s výslednou úspešnosťou vyťaženia údajov na úrovni 97,26% na jednotlivú vyťažovanú položku. Trvanie spracovania bolo skrátené podľa požiadaviek klienta</p>
            </div>
                <div className='bonusInfo'>
                    <div>
                        <img src={require('./img/sporitelna.jpg')}></img>
                        <h3>Klient</h3>
                        <p>Slovenská sporiteľňa</p>
                    </div>
                    <div>
                        <h3>Opis projektu</h3>
                        <p>Výpomoc pri digitalizácii dokumentov v Slovenskej sporiteľni pomocou technológie DocMiner</p>
                        <h3>Výsledok</h3>
                        <p>Riešenie vysoko prekonalo požadovanú úspešnosť spracovania a pomohlo klientovi pri digitalizácii dokumentov</p>
                    </div>
                    <div>
                        <h3>Použité technológie</h3>
                        <div className='imgCont'>
                            <img src={require('./img/abby.png')}></img>
                            <img src={require('./img/java.png')}></img>
                         </div>
                    </div>
            </div>
            </div>
        </div>
        )
    }
}

export default Sporitelna