import React from 'react'
import MenuClick from './MenuClick.jsx'
import { Link } from "react-router-dom";

const logo = require('./logo.png')
const menuData = [{
    id: 2,
    name:'Naše Služby',
    href:'/sluzby'
},{
    id: 3,
    name:'Využitie',
    href:'/vyuzitie'
},{
    id: 4,
    name:'Referencie',
    href:'/referencie'
},{
    id: 5,
    name:'Výskumné projekty',
    href:'/vyskumneprojekty'
},{
    id:6,
    name:'Kontakt',
    href:'/kontakt'
}
]


class Menu extends React.Component{
    
    constructor(){
        super()
        this.myRef = React.createRef()
        this.state = {hideMenu:false}
        this.hideMenu = this.hideMenu.bind(this)
    }
    
    
    hideMenu(){
        //this.setState({hideMenu:true})
        this.myRef.current.checked = false
    }
    
    
    
    render(){
        const menuItems = menuData.map(item => {
            return(<MenuClick key={item.id} id={item.id} hideMenu={this.hideMenu} name={item.name} href={item.href} active={item.id == this.props.activeSlide}/>)
        })
        return(
            <nav id="navbar">
                    <input ref={this.myRef} type="checkbox" id="check" ></input>
            <label htmlFor="check" className="checkbtn">
                <div className="menubars">
                      <span></span>
                      <span></span>
                      <span></span>
                      <span></span>
                </div>
            </label>
                <Link to='/home' ><img src={logo} alt='MindIT logo' className='logo-mobile' /></Link>
            <ul className="menu">
                
                <Link to='/home' style={{display:'inline'}}>
                    <img src={logo} alt='MindIT logo' className='logo'></img></Link>
                    {menuItems}
               
            </ul>
            </nav>
        )
    }
}

export default Menu