import React , {useState} from 'react'
import Slider from './components/slider/Slider.jsx'
import Menu from './components/menu/Menu.jsx'
import MainContent from './MainContent.jsx'
import SlideUpButton from './components/SlideUpButton.jsx'
import {
  HashRouter as Router, //TODO zmenit na BrowserRouter
  Switch,
  Route,
  Link,
  useParams
} from "react-router-dom";


class App extends React.Component{
    constructor(){
        super()
        this.state = {
            activeSlide:0
        }
        this.setActiveSlide = this.setActiveSlide.bind(this)
    }
    
    setActiveSlide(id){
        
        this.setState({activeSlide:id})
    }
    
    render(){
        return(
            <Router>
            <div>
                <Menu 
                    activeSlide={this.state.activeSlide}
                    notifySlider={this.notifySlider}/>
                <div className='header'>
                    <Switch>
                        <Route path="/:id">
                            <SliderWrapper data={this.state}  setActiveSlide={this.setActiveSlide}/>
                        </Route>
                    </Switch>
                </div>
                <SlideUpButton />
            </div>
            </Router>
        )
    }
}

function SliderWrapper(props){
    let { id } = useParams()
    let [ state, setState ] = useState({
        currentScreen : 1,
        previousScreen: 0,
        swipeState : 0,
    })
    
    let avoidClick = false
    
    let getCurrentScreen = () =>{
        let destinations = ['home','sluzby','vyuzitie','referencie','vyskumneprojekty','kontakt']
        for (const [index, dest] of destinations.entries()) {
            if (dest==id){
                return index + 1
            }
        }
    }
    
    let notifySlider = (slideId) =>{
        if (state.currentScreen != slideId && !avoidClick){
            props.setActiveSlide(slideId)
            handleSwipe(slideId)
        }
    }
    
    let handleSwipe = (currentScreen)=>{
        if (!avoidClick){
            setState((prevState) => {
            if (currentScreen < state.currentScreen){
                return {
                       previousScreen: prevState.currentScreen,
                        swipeState:1,
                       currentScreen: currentScreen
                      }
            }
            else{
                return {
                       previousScreen: prevState.currentScreen,
                        swipeState:2,
                       currentScreen: currentScreen
                      }
            }
        })
        
        avoidClick = true
        
        setTimeout(() => {
        setState((prevState) => 
                 {
                return{
                    ...prevState,
                    swipeState:0
                }})
                avoidClick = false
                }, 500)
        }
    }
    notifySlider(getCurrentScreen())
    return (
        <Slider data={state} notifySlider={props.notifySlider} ></Slider>
    )
}


export default App
    
